<!-- eslint-disable vue/no-v-model-argument -->
<template>
  <div>
    <v-card max-width="1000px" class="mx-auto mt-5">
      <v-data-table
        :expanded.sync="expanded"
        :headers="headers"
        :items="professores"
        :hide-default-footer="true"
        disable-pagination
        disable-filtering
        disable-sort
        show-expand
      >
        <template v-slot:top>
          <v-toolbar flat dense>
            <v-toolbar-title>Aulas</v-toolbar-title>
            <v-spacer />
            <v-btn
              color="primary"
              dark
              plain
              @click="openDeleteDialogLectures(null)"
            >
              Limpar
            </v-btn>
            <v-btn color="primary" dark plain @click="expandAll">
              Expandir todos
            </v-btn>
            <v-btn color="primary" dark plain @click="collapseAll">
              Fechar todos
            </v-btn>
            <v-col cols="3" v-if="turnos != null && turnos.length > 1">
              <v-select
                v-model="turnoSelecionado"
                :items="turnos"
                item-text="name"
                item-value="id"
                hide-details
              />
            </v-col>
          </v-toolbar>
        </template>

        <!-- eslint-disable-next-line -->
        <template v-slot:item.number_lectures="{ item, index }">
          <span :key="keys[index] || index">{{
            getTotalLectures(item.id)
          }}</span>
        </template>

        <!-- eslint-disable-next-line -->
        <template v-slot:item.actions="{ item }">
          <v-btn
            color="primary"
            dark
            outlined
            @click="prepareNewSubjectDialog(item.id)"
          >
            Vincular matéria
          </v-btn>
          <v-btn
            color="primary"
            dark
            outlined
            class="ml-2"
            @click="openDeleteDialogLectures(item.id)"
          >
            Limpar
          </v-btn>
        </template>

        <!-- eslint-disable-next-line vue/no-unused-vars -->
        <template v-slot:expanded-item="{ headers, item, index }">
          <td :colspan="headers.length">
            <v-data-table
              :headers="internalTableHeaders"
              :items="getInternalTableItems(item.id)"
              :hide-default-footer="true"
              disable-pagination
              disable-filtering
              disable-sort
              :key="reloadInternalTable"
            >
              <!-- <template v-slot:top>
                <v-toolbar flat dense>
                  <v-toolbar-title
                    >Aulas de {{ item.name }} em
                    {{ turnosLookup[turnoSelecionado] }}</v-toolbar-title
                  >
                  <v-icon
                    class="ml-1"
                    @click="openDeleteDialogLectures(item.id)"
                    >mdi-delete</v-icon
                  >
                  <v-spacer />
                  <v-btn
                    color="primary"
                    dark
                    class="mb-2"
                    @click="prepareNewSubjectDialog(item.id)"
                  >
                    Vincular matéria
                  </v-btn>
                </v-toolbar>
              </template> -->
              <template
                v-for="(sala, index) in salasTurnoSelecionado"
                v-slot:[`item.${sala.id}`]="props"
              >
                <v-text-field
                  :key="index"
                  type="number"
                  step="1"
                  min="0"
                  :value="props.item[sala.id]"
                  @input="
                    updateLectures(
                      $event,
                      item.id,
                      props.item.id_materia,
                      sala.id,
                      index
                    )
                  "
                  @keydown="validateKey($event)"
                  flat
                  dense
                  class="myInput"
                ></v-text-field>
              </template>

              <!-- eslint-disable-next-line vue/valid-v-slot -->
              <template v-slot:item.actions="props">
                <v-icon
                  small
                  class="mr-2"
                  @click="deleteSubject(item.id, props.item.id_materia)"
                >
                  mdi-delete
                </v-icon>
              </template>
            </v-data-table>
          </td>
        </template>
      </v-data-table>
    </v-card>
    <v-dialog v-model="dialogDeleteLectures" max-width="700px">
      <v-card>
        <v-card-title class="text-h6">{{ deleteLecturesTitle }}</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" @click="dialogDeleteLectures = false"
            >Cancelar</v-btn
          >
          <v-btn color="red" class="white--text" @click="clearLectures"
            >Zerar aulas</v-btn
          >
          <v-btn color="red" class="white--text" @click="unlinkSubjects"
            >Excluir aulas e desvincular matérias</v-btn
          >
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="text-h5">Vincular matéria</span>
        </v-card-title>

        <v-card-text>
          <v-container>
            <v-form @submit.prevent ref="form">
              <v-row>
                <v-col cols="12" sm="6" md="4">
                  <v-select
                    v-model="editedItem.id_materia"
                    label="Matéria"
                    :items="materias"
                    item-text="name"
                    item-value="id"
                    :rules="[rules.required]"
                  ></v-select>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="close"> Cancelar </v-btn>
          <v-btn color="blue darken-1" text @click="saveItem"> OK </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialogDelete" max-width="500px">
      <v-card>
        <v-card-title class="text-h5">Desvincular matéria?</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="closeDelete"
            >Cancelar</v-btn
          >
          <v-btn color="blue darken-1" text @click="deleteSubjectConfirm"
            >OK</v-btn
          >
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import helper from "@/mixins/helper";
import validationMixin from "@/mixins/validation";

export default {
  name: "Quantidades",
  props: ["id"],
  mixins: [helper, validationMixin],
  components: {},

  data() {
    return {
      aulas: {},
      items: {},
      externalTableItems: [],
      dialog: false,
      dialogDelete: false,
      editedIndex: -1,
      editedItem: { id_professor: null, id_materia: null },
      defaultItem: { id_professor: null, id_materia: null },
      expanded: [],
      reloadInternalTable: 0,
      selectedTeacher: null,
      selectedSubject: null,
      dialogDeleteLectures: false,
      keys: [],
    };
  },

  mounted() {
    this.load();
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  computed: {
    headers() {
      return [
        {
          text: "Professor",
          value: "name",
          align: "center",
        },
        {
          text: "Aulas no turno",
          value: "number_lectures",
          align: "center",
        },
        { text: "", value: "data-table-expand", align: "center" },
        { text: "", value: "actions", width: "400", align: "center" },
      ];
    },
    internalTableHeaders() {
      return Array({
        text: "Matéria",
        value: "name",
        width: "10px",
        align: "center",
      })
        .concat(
          Array.from(this.salasTurnoSelecionado, (sala) => {
            return { text: sala.name, value: sala.id.toString() };
          })
        )
        .concat([{ text: "Ações", value: "actions", sortable: false }]);
    },
    deleteLecturesTitle() {
      if (this.selectedTeacher != null) {
        return (
          "Confirmação - Excluindo aulas de " +
          this.professoresLookup[this.selectedTeacher] +
          " no turno selecionado"
        );
      }
      return "Confirmação - Excluindo aulas do turno selecionado";
    },
    maxKey() {
      return Math.max(...this.keys);
    },
  },

  methods: {
    async load(isReload = false) {
      if (!this.id) {
        return;
      }
      try {
        this.setLoading(isReload);
        await this.getStructure();
        this.keys = this.professores.map((_, index) => index);
        const resp = await this.$axios.get("/aulas/" + this.id);
        const { aulas } = resp.data;
        this.saved();

        const items = {};
        for (let linha of aulas) {
          const { id_sala, id_professor, id_turno, id_materia, numero_aulas } =
            linha;
          if (!Object.hasOwn(items, id_turno)) {
            items[id_turno] = {};
          }

          if (!Object.hasOwn(items[id_turno], id_professor)) {
            items[id_turno][id_professor] = {};
          }

          if (!Object.hasOwn(items[id_turno][id_professor], id_materia)) {
            items[id_turno][id_professor][id_materia] = {};
          }
          items[id_turno][id_professor][id_materia][id_sala] = numero_aulas;
          this.items = items;
        }
        this.fillBlankCells();
      } catch (e) {
        console.error(e);
      } finally {
        this.clearLoading();
      }
    },

    fillBlankCells() {
      for (const id_turno in this.items) {
        for (const id_professor in this.items[id_turno]) {
          for (const id_materia in this.items[id_turno][id_professor]) {
            for (const sala of this.salas) {
              if (sala.turno !== parseInt(id_turno, 10)) {
                continue;
              }

              if (
                this.items[id_turno][id_professor][id_materia][sala.id] == null
              ) {
                this.items[id_turno][id_professor][id_materia][sala.id] = 0;
              }
            }
          }
        }
      }
    },

    getInternalTableItems(id_professor) {
      let linhas = [];
      if (
        Object.hasOwn(this.items, this.turnoSelecionado) &&
        Object.hasOwn(this.items[this.turnoSelecionado], id_professor)
      ) {
        const ids_materias = Object.keys(
          this.items[this.turnoSelecionado][id_professor]
        );
        ids_materias.sort((a, b) => {
          if (this.materiasLookup[a] < this.materiasLookup[b]) {
            return -1;
          }
          if (this.materiasLookup[a] > this.materiasLookup[b]) {
            return 1;
          }
          return 0;
        });

        for (let id_materia of ids_materias) {
          const linha = {
            id_materia,
            name: this.materiasLookup[id_materia],
            ...this.items[this.turnoSelecionado][id_professor][id_materia],
          };
          linhas.push(linha);
        }
      }
      return linhas;
    },

    getTotalLectures(id_professor) {
      let total = 0;
      if (
        this.items[this.turnoSelecionado] &&
        this.items[this.turnoSelecionado][id_professor]
      ) {
        for (const id_materia in this.items[this.turnoSelecionado][
          id_professor
        ]) {
          for (const id_sala in this.items[this.turnoSelecionado][id_professor][
            id_materia
          ]) {
            total += Number(
              this.items[this.turnoSelecionado][id_professor][id_materia][
                id_sala
              ]
            );
          }
        }
      }
      return total;
    },

    save(reload = true) {
      const { items: linhas_aulas } = this;
      this.$axios
        .post("/aulas", {
          id_configuracao: this.id,
          linhas_aulas,
        })
        .then((response) => {
          if (response.status === 200 && reload) {
            this.load(true);
          }
        })
        .catch((err) => console.log(err));
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    saveItem() {
      if (!this.validate()) {
        return;
      }

      this.changed();
      const { id_professor, id_materia } = this.editedItem;
      if (!Object.hasOwn(this.items, this.turnoSelecionado)) {
        this.items[this.turnoSelecionado] = {};
      }
      if (!Object.hasOwn(this.items[this.turnoSelecionado], id_professor)) {
        this.items[this.turnoSelecionado][id_professor] = {};
      }
      if (
        !Object.hasOwn(
          this.items[this.turnoSelecionado][id_professor],
          id_materia
        )
      ) {
        this.items[this.turnoSelecionado][id_professor][id_materia] = {};
      }
      this.salasTurnoSelecionado.forEach((sala) => {
        if (
          !Object.hasOwn(
            this.items[this.turnoSelecionado][id_professor][id_materia],
            sala.id
          )
        ) {
          this.items[this.turnoSelecionado][id_professor][id_materia][
            sala.id
          ] = 0;
        }
      });
      this.reloadInternalTable += 1;
      this.close();
    },

    prepareNewSubjectDialog(id_professor) {
      this.setSelectedTeacher(id_professor);
      this.editedItem.id_professor = id_professor;
      this.resetValidation();
      this.dialog = true;
    },

    setSelectedTeacher(id_professor) {
      this.selectedTeacher = id_professor;
    },

    setSelectedSubject(id_materia) {
      this.selectedSubject = id_materia;
    },

    deleteSubject(id_professor, id_materia) {
      this.setSelectedTeacher(id_professor);
      this.setSelectedSubject(id_materia);
      this.dialogDelete = true;
    },

    deleteSubjectConfirm() {
      delete this.items[this.turnoSelecionado][this.selectedTeacher][
        this.selectedSubject
      ];
      this.reloadInternalTable += 1;
      this.dialogDelete = false;
      this.changed();
    },

    updateLectures(value, id_professor, id_materia, id_sala, index) {
      this.checkChanges(
        this.items[this.turnoSelecionado][id_professor][id_materia][id_sala],
        value
      );
      this.items[this.turnoSelecionado][id_professor][id_materia][id_sala] =
        value;
      const nextKey = Math.max(...this.keys) + 1;
      this.$set(this.keys, index, nextKey);
    },

    validateKey(e) {
      //allow backspace
      const allowedKeyCodes = [8];
      if (allowedKeyCodes.includes(e.keyCode)) {
        return;
      }
      if (isNaN(e.key) || !e.key.trim().length) {
        e.preventDefault();
      }
    },

    openDeleteDialogLectures(teacherId) {
      this.setSelectedTeacher(teacherId);
      this.dialogDeleteLectures = true;
    },

    clearLecturesFromTeacher(teacherId) {
      if (teacherId != null) {
        for (const subjectId in this.items[this.turnoSelecionado][teacherId]) {
          for (const classId in this.items[this.turnoSelecionado][teacherId][
            subjectId
          ]) {
            this.items[this.turnoSelecionado][teacherId][subjectId][
              classId
            ] = 0;
          }
        }
      }
    },

    clearLectures() {
      this.changed();
      this.dialogDeleteLectures = false;
      if (this.selectedTeacher != null) {
        this.clearLecturesFromTeacher(this.selectedTeacher);
      } else {
        for (const teacher of this.professores) {
          this.clearLecturesFromTeacher(teacher.id);
        }
      }
      this.reloadInternalTable += 1;
    },

    unlinkSubjectsFromTeacher(teacherId) {
      if (teacherId != null) {
        this.items[this.turnoSelecionado][teacherId] = {};
      }
    },

    unlinkSubjects() {
      this.changed();
      this.dialogDeleteLectures = false;
      if (this.selectedTeacher != null) {
        this.unlinkSubjectsFromTeacher(this.selectedTeacher);
      } else {
        for (const teacher of this.professores) {
          this.unlinkSubjectsFromTeacher(teacher.id);
        }
      }
      this.reloadInternalTable += 1;
    },

    expandAll() {
      this.expanded = [...this.professores];
    },

    collapseAll() {
      this.expanded = [];
    },
  },
};
</script>

<style scoped>
.myInput:deep() input {
  cursor: pointer;
}

.myInput:deep() .v-input__slot::before {
  border-style: none !important;
}

.myInput:deep() input::-webkit-outer-spin-button,
.myInput:deep() input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.myInput:deep() input[type="number"] {
  -moz-appearance: textfield;
}
</style>
